import { React, Fragment } from 'react';

import { useLocation } from 'react-router-dom';
import './lodging.scss';
export default function Lodging() {
  const data = useLocation().state;
  return (
    <Fragment>
      <p>Caroussel</p>
      <h1>{data.title}</h1>
      <p>{data.location}</p>
    </Fragment>
  );
}
